.promo {
  width: 100%;
  min-height: 463px;
  background-image:
    linear-gradient( $accent-bg-opacity07, $accent-bg-opacity07),
    url("../img/main/main-promo_large.jpg");

  @media (min-resolution: 2dppx) {
    background-image:
      linear-gradient( $accent-bg-opacity07, $accent-bg-opacity07),
      url("../img/main/main-promo_large@2x.jpg");
  }
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  .no-webp & {
    background-image:
      linear-gradient( $accent-bg-opacity07, $accent-bg-opacity07),
      url("../img/main/main-promo_large.jpg");

    @media (min-resolution: 2dppx) {
      background-image:
        linear-gradient( $accent-bg-opacity07, $accent-bg-opacity07),
        url("../img/main/main-promo_large@2x.jpg");
    }
  }

  .webp & {
    background-image:
      linear-gradient( $accent-bg-opacity07, $accent-bg-opacity07),
      url("../img/main/main-promo_large.webp");

    @media (min-resolution: 2dppx) {
      background-image:
        linear-gradient( $accent-bg-opacity07, $accent-bg-opacity07),url("../img/main/main-promo_large@2x.webp");
    }
  }
}

.promo__content {
  min-height: inherit;

  @include display-flex-wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include cormorantgaramond-font;
  color: $basic-white;

  & h2 {
    margin: 0 0 20px;
    font-weight: 700;
    font-size: 45px;
    line-height: 1.3em;
    letter-spacing: 0.03em;
    text-transform: uppercase;
  }

  & P {
    margin: 0;
    font-weight: 400;
    font-size: 38px;
    font-style: italic;
    line-height: 1.3em;
  }
}
